.bet-input {
  border: none;
}
.bet-input .weui-cells.vux-no-group-title {
  margin-top: 0;
}
.bet-input .weui-cells.vux-no-group-title::before {
  display: none;
}
.bet-input .weui-cells.vux-no-group-title::after {
  display: none;
}
.bet-input .weui-cells.vux-no-group-title .weui-btn {
  overflow: visible;
}
.bet-input .weui-cells.vux-no-group-title .vux-x-input.weui-cell.weui-vcode {
  padding: 0 5px;
}
.bet-input .weui-cells.vux-no-group-title .vux-x-input.weui-cell.weui-vcode .weui-cell__bd.weui-cell__primary {
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  height: 40px;
  box-sizing: border-box;
}
.bet-input .weui-cells.vux-no-group-title .vux-x-input.weui-cell.weui-vcode .weui-cell__bd.weui-cell__primary input {
  height: 100%;
  box-sizing: border-box;
  padding-left: 5px;
}
.vux-popup-dialog .bet-content {
  font-size: 14px;
  text-align: left;
  width: 95%;
  background-color: #fff;
  margin: 0 auto;
  border-radius: 5px;
  padding: 10px 0;
}
.vux-popup-dialog .bet-content .title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.vux-popup-dialog .bet-content ul {
  height: 150px;
  overflow-y: auto;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
  list-style: none;
  color: #000;
}
.vux-popup-dialog .bet-content ul li {
  text-align: left;
  height: 25px;
  line-height: 25px;
}
.vux-popup-dialog .bet-content .total {
  height: 30px;
  line-height: 30px;
  margin-bottom: 10px;
}
.vux-popup-dialog .bet-content .amount {
  color: #d0021b;
}
.vux-popup-dialog .bet-content .loading {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  text-align: center;
}
.vux-popup-dialog .bet-content .loading .weui-loading {
  height: 30px;
  width: 30px;
}
.vux-popup-dialog .bet-content .buttons {
  height: 50px;
}
.input-errors {
  font-size: 14px;
  margin-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.input-errors .text {
  list-style: circle inside;
  color: #ff9800;
}
.text-danger {
  color: #d0021b;
}
.text-success {
  color: #7bb32e;
}
.vux-input-icon.weui-icon.weui_icon_warn.weui-icon-warn {
  display: none;
}
.emoji.vux-indicator.vux-indicator-center {
  bottom: 5px;
}
.emoji.vux-indicator.vux-indicator-center .vux-icon-dot.active {
  background: #9b9b9b;
}
.close-btn {
  position: relative;
  width: 30px;
  height: 30px;
}
.close-btn::before,
.close-btn::after {
  position: absolute;
  content: ' ';
  top: 5px;
  right: 15px;
  height: 20px;
  width: 2px;
  background-color: #fff;
}
.close-btn::before {
  transform: rotate(45deg);
}
.close-btn::after {
  transform: rotate(-45deg);
}

