.vux-tab-ink-bar {
  position: absolute;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #04BE02;
  text-align: center;
}
.vux-tab-ink-bar-transition-forward {
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1), left 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s;
}
.vux-tab-ink-bar-transition-backward {
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s, left 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.vux-tab-bar-top .vux-tab-ink-bar {
  top: 0;
}
.vux-tab {
  display: flex;
  background-color: #fff;
  height: 44px;
  position: relative;
}
.vux-tab button {
  padding: 0;
  border: 0;
  outline: 0;
  background: 0 0;
  appearance: none;
}
.vux-tab .vux-tab-item {
  display: block;
  flex: 1;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background: linear-gradient(180deg, #e5e5e5, #e5e5e5, rgba(229, 229, 229, 0)) bottom left no-repeat;
  background-size: 100% 1px;
  font-size: 14px;
  text-align: center;
  line-height: 44px;
  color: #666;
}
.vux-tab .vux-tab-item.vux-tab-selected {
  color: #04BE02;
  border-bottom: 3px solid #04BE02;
}
.vux-tab-bar-top .vux-tab .vux-tab-item {
  background: linear-gradient(180deg, #e5e5e5, #e5e5e5, rgba(229, 229, 229, 0)) top left no-repeat;
  background-size: 100% 1px;
}
.vux-tab-bar-top .vux-tab .vux-tab-item.vux-tab-selected {
  border-bottom: none;
  border-top: 3px solid #04BE02;
}
.vux-tab .vux-tab-item.vux-tab-disabled {
  color: #ddd;
}
.vux-tab.vux-tab-no-animate .vux-tab-item.vux-tab-selected {
  background: 0 0;
}
/** when=prop:custom-bar-width **/
.vux-tab-bar-inner {
  display: block;
  background-color: #04BE02;
  margin: auto;
  height: 100%;
  transition: width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.vux-tab-item-badge {
  position: absolute;
  top: 0;
  bottom: 0;
  box-sizing: border-box;
  display: inline-block;
  height: 18px;
  min-width: 18px;
  padding: 0 4px;
  border-radius: 30px;
  margin: auto 0 auto 4px;
  line-height: 18px;
  font-size: 11px;
  background-clip: padding-box;
  vertical-align: middle;
}
.vux-tab-wrap {
  position: relative;
  padding-top: 44px;
}
.vux-tab-container {
  height: 44px;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  position: absolute;
}
.scrollable {
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 17px;
  box-sizing: content-box;
}
.scrollable::-webkit-scrollbar {
  display: none;
}
.scrollable .vux-tab-ink-bar {
  bottom: 17px;
  position: absolute;
}
.scrollable .vux-tab-item {
  flex: 0 0 22%;
}

.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  animation: vux-dialog-out 0.3s;
}
@keyframes vux-dialog-in {
0% {
    transform: scale(1.185);
    opacity: 0;
}
100% {
    transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-out {
0% {
    transform: scale(1);
    opacity: 1;
}
100% {
    transform: scale(0.85);
    opacity: 0;
}
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  transition: opacity 300ms;
}
