.vux-tap-active {
  tap-highlight-color: rgba(0, 0, 0, 0);
  user-select: none;
}
.vux-tap-active:active {
  background-color: #ECECEC;
}
.weui-cells {
  margin-top: 1.17647059em;
  background-color: #FFFFFF;
  line-height: 1.41176471;
  font-size: 17px;
  overflow: hidden;
  position: relative;
}
.weui-cells:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.weui-cells:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.weui-cells__title {
  margin-top: 0.77em;
  margin-bottom: 0.3em;
  padding-left: 15px;
  padding-right: 15px;
  color: #999999;
  font-size: 14px;
}
.weui-cells__title + .weui-cells {
  margin-top: 0;
}
.weui-cells__tips {
  margin-top: .3em;
  color: #999999;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.weui-cell {
  padding: 10px 15px;
  position: relative;
  display: flex;
  align-items: center;
}
.weui-cell:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #D9D9D9;
  color: #D9D9D9;
  transform-origin: 0 0;
  transform: scaleY(0.5);
  left: 15px;
}
.weui-cell:first-child:before {
  display: none;
}
.weui-cell_primary {
  align-items: flex-start;
}
.weui-cell__bd {
  flex: 1;
}
.weui-cell__ft {
  text-align: right;
  color: #999999;
}
.vux-cell-justify {
  height: 1.41176471em;
}
.vux-cell-justify.vux-cell-justify:after {
  content: ".";
  display: inline-block;
  width: 100%;
  overflow: hidden;
  height: 0;
}
.weui-loading {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent url("data:image/svg+xml;base64,") no-repeat;
  background-size: 100%;
}
.weui-loading.weui-loading_transparent {
  background-image: url("data:image/svg+xml;base64,");
}
@-webkit-keyframes weuiLoading {
0% {
    transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes weuiLoading {
0% {
    transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    transform: rotate3d(0, 0, 1, 360deg);
}
}
.vux-cell-primary {
  flex: 1;
}
.vux-label {
  display: inline-block;
  word-wrap: break-word;
  word-break: break-all;
}
.weui-cell__ft .weui-loading {
  display: inline-block;
}
.weui-cell__ft.vux-cell-align-left {
  text-align: left;
}
.weui-cell.vux-cell-no-border-intent:before {
  left: 0;
}
.weui-cell_access .weui-cell__ft.vux-cell-arrow-down:after {
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0) rotate(90deg);
}
.weui-cell_access .weui-cell__ft.vux-cell-arrow-up:after {
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0) rotate(-90deg);
}
.vux-cell-arrow-transition:after {
  transition: transform 300ms;
}
.vux-cell-disabled .vux-label {
  color: #b2b2b2;
}
.vux-cell-disabled.weui-cell_access .weui-cell__ft:after {
  border-color: #e2e2e2;
}
